.patient {
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    max-width: 600px;
    padding: 20px;
    background-color: var(--background-secondary);
    border-radius: 20px;
    margin-bottom: 20px;
    position: relative;


}
.patient:hover {
    -webkit-filter: brightness(0.99);
    filter: brightness(0.99);
}
.patient p {
    font-size: 1.3rem;
    margin-bottom: 80px;
}

.patient img {
    max-width: 300px;
    min-width: 200px;
    width: 50%;
    height: auto;
    display: block;
    margin: 0px auto;
}

.patient .button{
    display: table-cell;
    flex: 1.0;
    background-color: var(--primary-accent);
    color: white;
    width: 250px;
    height: 50px;
    margin: 30px;
    font-size: 1.3rem;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    border-radius: 25px;
    box-shadow: 0 1px 2px 0 rgb(0, 0, 0);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.5);
}

.patient .button:hover{
    -webkit-filter: brightness(0.95);
    filter: brightness(0.90);
}

.patient .button:active {
    box-shadow: 0 1px 2px 0 rgb(0, 0, 0);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.patient a {
    text-decoration: none;
    padding: 10px;
    color: black;
}

.button-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    bottom: 10px;
}



@media screen and (max-width: 641px) {
    .patient p {
        margin-bottom: 160px;
    }
     
 }